<template>
  <div id="app">
    <!-- Navbar Start -->
    <b-navbar id="navbar" fixed="top">
      <b-container>
        <b-navbar-brand href="#" id="navbarBrand" to="/">
          debbroto 
          <vue-typed-js :strings="['<b>portfolio</b>']" :loop="true" :showCursor="false" id="navTypeJs">
            <span class="typing"></span>
          </vue-typed-js>
        </b-navbar-brand>
        <b-navbar-nav class="ml-auto" id="navbarRight">
          <b-nav-item to="career" id="careerLink"><b-spinner type="grow" label="Spinning" v-if="spiner['career']"></b-spinner>Career</b-nav-item>
          <b-nav-item to="skills" id="skillsLink"><b-spinner type="grow" label="Spinning" v-if="spiner['skills']"></b-spinner>Skills</b-nav-item>
          <b-nav-item to="portfolio" id="portfolioLink"><b-spinner type="grow" label="Spinning" v-if="spiner['portfolio']"></b-spinner>Portfolio</b-nav-item>
          <b-nav-item to="about" id="aboutLink"><b-spinner type="grow" label="Spinning" v-if="spiner['about']"></b-spinner>About</b-nav-item>
        </b-navbar-nav>
      </b-container> 
    </b-navbar>
    <!-- Navbar End -->
    <router-view v-on:career="careerHover" v-on:skills="skillsHover"/>
  </div>
</template>
<script>
import Vue from 'vue';
import VueTypedJs from 'vue-typed-js';
Vue.use(VueTypedJs);
export default {
  name: 'app',
  data: function(){
    return {
      spiner: {
        career: false,
        skills: false,
        portfolio: false,
        about: false
      },
    }
  },
  methods: {
    /******************** TODO SECTION START */
    /******************** Need Optimisation */
    careerHover: function(value){
      if(value == true){
        this.spiner['career'] = true;
      }
      if(value == false){
        this.spiner['career'] = false;
      }
    },
    skillsHover: function(value){
      if(value == true){
        this.spiner['skills'] = true;
      }else if(value == false){
        this.spiner['skills'] = false;
      }else{
        this.spiner['skills'] = true;
      }
    }
    /******************** TODO SECTION END */
  }
}
</script>
<style>
/** Global Fonts */
* {
  font-family: 'Montserrat', sans-serif;
}
/** Hide Scroll Bar */
::-webkit-scrollbar { 
  display: none; 
}

/** Navbar Design Start */
#navbar{
  background-color: #fff;
  padding: 20px 15px;
  border-bottom: 1px solid #d2d2d2;
}

#navbar #navbarBrand{
  text-transform: capitalize;
  font-size: 1rem;
  margin-left: 20px;
}
#navbarBrand #navTypeJs{
  display: inline-block;
}

#navbar #navbarRight{
  margin-right: 20px;
}
#navbarRight #careerLink, #portfolioLink, #skillsLink, #aboutLink{
  position: relative;
  margin-left: 15px;
  display: inline-block;
  font-size: 1rem;
  line-height: inherit;
  white-space: nowrap;
}
/** Spiner Custom */
.spinner-grow {
  position: absolute;
  left: -20px;
  top: 3px;
}

/** Navbar Media */
@media (min-width: 1200px){
/* start of desktop styles */
  
}
@media screen and (max-width: 991px) {
/* start of large tablet styles */
  #navbar #navbarRight{
    display: none;
    margin-right: 0;
  }
}
@media screen and (max-width: 767px) {
/* start of medium tablet styles */
  #navbar #navbarBrand{
    margin-left: 0;
  }
}
@media screen and (max-width: 479px) {
/* start of phone styles */
  
}
/** Navbar Design End */
</style>



