<template>
  <div>
    <!-- Loader -->
    <div class="lds-ring" v-if="loader"><span id="loaderText">Loading</span><div></div><div></div><div></div><div></div></div>
    <!-- Career Section Start -->
    <b-container id="careerSection" @mouseover="careerHover(true)" @mouseout="careerHover(false)" v-if="dataLoadSignal">
      <b-row>
        <b-col cols="12" md="6">
          <div id="careerContent">
            <h1>Career Objective</h1>
            <div class="career-info">
              <p>
                I am passionate programmer (ACMICPC contestant) and possess sound knowledge of Javascript, Vue JS, Nuxt JS, Vuetify, React JS, JQuary, Python, PHP,
                DBMS(SQL Server, MySql), Version Control (git), HTML5, CSS3, Bootstrap, Stylus, Automation and web development methodology related fields.
              </p>
              <p>I want to apply my knowledge and skills to help achieve the business goal of the company.</p>
            </div>
            <router-link to="career">
              <a class="button">See more</a>
            </router-link>
          </div>
        </b-col>
        <b-col cols="12" md="6">
          <div id="careerContent">
            <div class="code">
<pre><code class="language-js">{
  "name": "Debbroto Sarkar",
  "occupation": "Web Developer",
  "available_for_hire:" true
}
</code></pre>
            </div>
          </div>
        </b-col>
      </b-row>
    </b-container>
    <!-- Career Section End -->

    <!-- Skill Section Start -->
    <div id="skillSection" @mouseover="skillsHover(true)" @mouseout="skillsHover(false)" v-if="dataLoadSignal">
      <b-container>
        <h1>Technical Skills</h1>
        <p>This comes from having a decent understanding of all the code as well as being able to read the Perl scripts.</p>
        <isotope :options="option1" :list="list">
          <div v-for="element in list" :key="element.id">
            {{element.name}}
          </div>
        </isotope>
      </b-container>
    </div>
    <!-- Skill Section End -->
    <div>
      <h1 style="text-align: center; background-color: red; color: #fff; padding: 10px;">Development Status : In Progress</h1>
    </div>
  </div>
</template>
<script>
import { db, homePageStatus } from '../firebase.js';
import Vue from 'vue';
import VueTypedJs from 'vue-typed-js';
import VueFlip from 'vue-flip';
import isotope from 'vueisotope';

Vue.use(VueTypedJs);
export default {
  components: {
    'vue-flip': VueFlip,
    isotope,
  },
  data() {
    return {
      jobRetriveData: [],
      loader: true,
      dataLoadSignal: false,
      list:[
        {id: 1, name: 'JavaScript'},
        {id: 2, name: 'Vue JS'},
        {id: 3, name: 'Nuxt JS'}
      ],
      option1:{
        id : "first",
        getSortData: {
          id: "id",
          name: function(itemElem){
            return itemElem.name.toLowerCase();
          }
        },
        getFilterData:{
          isEven: function(itemElem){
            return itemElem.id % 2 === 0;
          },
          isOdd: function(itemElem){
            return itemElem.id % 2 !== 0;
          }
        }
		  }
    }
  },
  firestore(){
    return {
      jobRetriveData: db.collection('jobs').orderBy('createdAt','asc')
    }
  },
  methods: {
    careerHover: function(value){
      if(value == true){
        this.$emit('career', true);
      }
      if(value == false){
        this.$emit('career', false);
      }
    },
    skillsHover: function(value){
      if(value == true){
        this.$emit('skills', true);
      }
      if(value == false){
        this.$emit('skills', false);
      }
    }
  },
  watch: {
    'jobRetriveData': function(){
      if(this.jobRetriveData.length > 1){
        this.loader = false;
        this.dataLoadSignal = true;
      }
    }
  }
}
</script>
<style scoped>
/** Global */
h1 {
  font-size: 2.5rem; 
}
h1,
h2,
h3,
h4,
h5 {
  line-height: 1.2;
  margin-top: 0;
  margin-bottom: 1.5rem;
  font-weight: 400;
  letter-spacing: -.03rem; 
  color:#444;
}

/** Career Section Start */
#careerSection{
  margin-top: 35px;
  padding: 70px 15px 50px;
}
#careerContent .career-info{
  font-size: 1.1rem;
  color:#666;
  font-weight: inherit;
}
.code {
  margin: auto; 
  padding-top: 90px;
}
pre {
  background: #0D0D0D;
  color: #04ffa2;
  font-family: Menlo, Roboto Mono, Courier New, monospace;
  direction: ltr;
  text-align: left;
  white-space: pre;
  word-spacing: normal;
  word-break: normal;
  line-height: 1.6;
  font-weight: 400;
  -moz-tab-size: 2;
  -o-tab-size: 2;
  tab-size: 2;
  border-radius: 0;
  margin: 1.5rem 0rem 1.5rem 3rem;
  padding: 2rem;
  border-radius: 6px;
  border-top: 20px solid #d0d0d0;
  -webkit-font-smoothing: subpixel-antialiased;
  -webkit-box-shadow: 2px 3px 30px rgba(0, 0, 0, 0.15);
  box-shadow: 2px 3px 30px rgba(0, 0, 0, 0.15); 
}
code {
  font-family: Menlo, Roboto Mono, Courier New, monospace; 
  overflow: auto; 
}
a.button,
  [type=button],
  [type=submit] {
    display: inline-block;
    -webkit-appearance: none;
    -webkit-transition: background 500ms ease;
    transition: background 500ms ease;
    background-image: -webkit-gradient(linear, left top, right top, from(#33C7EF), color-stop(51%, #37F093), to(#33C7EF));
    background-image: linear-gradient(to right, #33C7EF 0%, #37F093 51%, #33C7EF 100%);
    background-size: 300% auto;
    color: white !important;
    border: none;
    border-radius: 6px;
    text-align: center;
    vertical-align: middle;
    padding: .75rem 1.50rem;
    font-size: 1.1rem;
    font-weight: 700;
    -webkit-box-shadow: 2px 3px 5px rgba(0, 0, 0, 0.05);
    box-shadow: 2px 3px 5px rgba(0, 0, 0, 0.05);
    cursor: pointer; }
    a.button:hover,
    [type=button]:hover,
    [type=submit]:hover {
      text-decoration: none;
      background-position: right center; 
    }
/** Career Section End */

/** Skill Section Start */
#skillSection{
  background-color: #f1f3f5;
  padding: 40px 0;
}
#skillSection h1{
  text-align: center;
}
#skillSection p{
  font-size: 1.2rem;
  color: #666;
  max-width: 700px;
  margin: 0 auto;
  text-align: center;
}
/** Skill Section end */

/** Page Media Query Start */
@media (min-width: 1200px){
  /** Career Section */
  #careerSection{
    margin-top: 70px;
    padding: 70px 0 80px;
  }
  #careerSection #careerContent{
    padding: 0 20px;
  }

  /** Skill Section */
  #skillSection{
    background-color: #f1f3f5;
    padding: 72px 0;
  }
}
@media screen and (max-width: 991px) {
  
}

@media screen and (max-width: 767px) {
  /** Career Section */
  pre{
    margin: 1.5rem 0 0;
  }
  .code {
    padding-top: 1.5rem;
  }
}

@media screen and (max-width: 479px) {
  /** Career Section */
  pre{
    margin: 1.5rem 0 0;
  }
  .code {
    padding-top: 1.5rem;
  }
}
/** Page Media Query End */

/** LOADER START */
/** Loader Custom */
.lds-ellipsis, .lds-ring{
  z-index: 1;
  position:absolute;
  left:0;
  right:0;
  margin-left:auto;
  margin-right:auto;
  top: 150px;
}

/** Loader Default */
.lds-ring {
  display: inline-block;
  width: 64px;
  height: 64px;
}
#loaderText{
  position: relative; 
  top: 100px; 
  color: #6c757d;
}
.lds-ring div {
  box-sizing: border-box;
  display: block;
  position: absolute;
  width: 51px;
  height: 51px;
  margin: 6px;
  border: 2px solid #6c757d;
  animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
  border-color: #6c757d transparent transparent transparent;
}
.lds-ring div:nth-child(1) {
  animation-delay: -0.45s;
}
.lds-ring div:nth-child(2) {
  animation-delay: -0.3s;
}
.lds-ring div:nth-child(3) {
  animation-delay: -0.15s;
}
@keyframes lds-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
/** LOADER END */
</style>



